/*--------------------header----------------------*/
.header {
	padding: rem(24) rem(100) rem(24) rem(20);
	display: flex;
	justify-content: space-between;
	align-items: center;
	column-gap: rem(16);

	@include max-screen($sm){
		padding-right: rem(16);
	}
	@include max-screen(em(700)){
		flex-direction: column;
		row-gap: rem(24);
		align-items: flex-start;
	}
}
.header__security {
	display: flex;
	align-items: center;
	column-gap: rem(11);
}
.security-header {}
.security-header__icon {
		height: rem(18);
	svg{
		width: rem(17);
		height: rem(18);
	}
}
.security-header__text {
	font-family: $med;
}
.header__actions {
	display: flex;
	column-gap: rem(13);

	@include max-screen(em(700)){
		max-width: calc(100% + rem(16));
		overflow: auto;
		position: relative;
		padding-right: rem(16);
		// right: rem(-16);
		// margin-left: rem(-16);
	}
}
.actions-header {}
.actions-header__item {
	@include but-null();
	display: flex;
	align-items: center;
	column-gap: rem(12);
	padding: rem(10) rem(24) rem(12) rem(28);
	flex-shrink: 0;
	border-radius: rem(40);
	@include vertical-grad(#213953, #1B2D41);
	text-decoration: none;
	color: #fff;
	transition: background-color .2s;

	@media (any-hover: hover) { //только для устройств с ховером
			// cursor: pointer;
			&:hover{
				@include vertical-grad(transparent, transparent);
				background-color: $accent;
			}
		}
	.icon{
		height: rem(18);
	}
	.icon-profile{
		width: rem(18);
		height: rem(20);
	}
	.icon-exit{
		width: rem(18);
	}
}
.item-actions-header {}
.item-actions-header__icon {
	height: rem(18);
}
.item-actions-header__money {
	font-family: $bold;
}
.item-actions-header__val {
	font-family: $light;
}
/*--------------------END header----------------------*/ 